import { callEnoviaWebService } from "./helpers";
// // Fetches document IDs based on product ID and document name
export const getAttachedDocumentIds = async (
  baseUrl,
  productId,
  headers,
  parentRelName,
  docName
) => {
  try {
    const url = `${baseUrl}/resources/v1/modeler/documents/parentId/${productId}?parentRelName=${parentRelName}&tenant=OI000186152`;
    const response = await callEnoviaWebService("GET", url, "", headers);

    if (response?.status && response?.output?.data?.length) {
      console.log("docName", docName);
      console.log("docName", response.output.data);
      if (docName) {
        const filteredData = response.output.data.filter(item =>
          item?.dataelements?.name.toLowerCase().includes(docName.toLowerCase())
        );
        console.log("Filtered Data:", filteredData);
        return filteredData.map(item => item.id);
      } else {
        return response.output.data.map(item => item.id);
      }
    }

    return [];
  } catch (error) {
    console.error("Error fetching document IDs:", error);
    return [];
  }
};

// Helper function to build disconnect payload
export  const buildDisconnectPayload = (docIds, parentId, docType) => {
  const data = docIds.map((id) => ({
    id,
    updateAction: "NONE",
    relateddata: {
      parents: [{ id: parentId, updateAction: "DISCONNECT" }],
    },
  }));
  return { data, documentType: docType };
};

// Helper function to disconnect documents API
export const disconnectDocuments = async (docs, headers, baseUrl, showErrorToast) => {
  console.log("Disconnecting documents:", docs);
  const results = [];
  for (const item of docs) {
    const url = `${baseUrl}/resources/v1/modeler/documents?parentRelName=${item.documentType}&parentDirection=from&tenant=OI000186152`;
    try {
      const res = await callEnoviaWebService("POST", url, item, headers);
      console.log(`Disconnected ${item.documentType}`, res);
    } catch (err) {
      console.error("Error disconnecting documents:", err)
      results.push(item);
    }
    return results;
  }
};